/* Tabs.css */
body, html {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  .tab-buttons {
    display: flex;
    justify-content: center;
    margin-top: 0;
    padding: 10px;
    background-color: #f8f9fa;
    position: sticky; /* To keep the buttons at the top */
    top: 0;
    z-index: 1000; /* Ensures buttons are on top of content */
  }
  
  .tab-button {
    padding: 10px 20px;
    margin-right: 5px;
    border: none;
    background-color: #f1f1f1;
    cursor: pointer;
    font-size: 16px;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .tab-button.active {
    background-color: #007bff;
    color: white;
  }
  
  .tab-button:hover {
    background-color: #1111e8;
  }
  
  .tab-content {
    margin-top: 20px;
    padding: 20px;
    border-top: 1px solid #ddd;
    text-align: center;
    background-color: #fff;
    height: 400px; /* Adjust based on the content height */
  }
  